<template>
    <div>
        <div class="meal-cell-up">
            <span class="iconfont icon-face"></span>
            <span class="meal-name">{{goods.names}}</span>
            <span class="iconfont icon-toright"></span>
        </div>
        <div class="meal-cell-middle">
            <div class="right-float">
                <span class="price">￥{{goods.price}}</span>
            </div>
            <div class="cell-info">
                <div class="cell-info-left">
                    <my-image v-if="goods.type.toString()=='10'"  class="my-img-meal" :imgUrl="goods.url"></my-image>
                    <my-image v-else  class="my-img-common" :imgUrl="goods.url"></my-image>
                </div>
                <div class="cell-info-right">
                    <p class="dec">{{goods.dec}}</p>
                    <p class="buy-time">购买时间：{{goods.buytm}}</p>
                    <span class="medal">正品</span>
                    <span class="medal">优质服务</span>
                </div>
            </div>
        </div>
        <div class="meal-cell-down">
            <template v-if="role.toString()=='1'">
                <div v-show="!goods.hadUesed" class="middle-item click-item">查看礼品券</div>
                <div v-show="goods.hadUesed" class="middle-item click-item dark">已使用</div>
            </template>
            <template v-else>
                <template v-if="goods.hadGive==true">
                    <div class="middle-item had-used">已转赠给：{{goods.giveNum}}</div>
                </template>
                <template v-else>
                    <div v-show="state=='1'||state=='4'" class="middle-item click-item" @click="giveHandle">赠送给客户</div>
                    <div v-show="state=='1'" class="middle-item click-item left" @click="backMoneyHandle">申请退款</div>
                    <div v-show="state=='2'" @click="cencelHandle" class="middle-item click-item dark">正在申请，是否取消</div>
                    <div v-show="state=='3'" class="middle-item tip">已退款</div>
                </template>
            </template>
        </div>
    </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);
import myImage from "../lazyloadImg/lazyImage.vue";

export default {
  name: "meal-cell",
  data() {
    return {};
  },
  props: {
    goods: {
      type: Object,
      default: function() {
        return {
          url: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          names: "380美容套餐",
          dec: "",
          buytm: "2018-01-20",
          passtm: "2018-02-20",
          price: 333,
          hadUesed: true,
          id: "",
          buyid:"",
          hadGive: false,
          giveNum:"",
          giveName:"",
          iscanreturn: false,
          orderstate: "0", //0未使用、1使用中、2已经使用过、3申请退款、4已经退款
          serialnum: "",
          type:10
        };
      }
    }
  },
  components: {myImage},
  activated() {},
  created() {
      
  },
  computed: {
    ...mapState(["openid", "path", "role"]),
    state() {
      let orderstate = this.goods.orderstate;
      let iscanreturn = this.goods.iscanreturn;
      if (iscanreturn == true) {
        if (orderstate == "0") return "1";
        else if (orderstate == "3") return "2";
        else if (orderstate == "4") return "3";
      } else {
        if (orderstate == "0") return "4";
        else if (orderstate == "3") return "2";
        else if (orderstate == "4") return "3";
      }
    }
  },
  methods: {
    giveHandle(event) {
      this.$parent.$parent.giveBtnClickHandle(this.goods);
      event.stopPropagation();
    },
    backMoneyHandle(event) {
      event.stopPropagation();
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type:'exitgoods',
          buyid:this.goods.buyid,
          serialnum:this.goods.serialnum
        })
        .then(response => {
          let irv = response.body.error.toString();
          if(irv=="ok"){
            this.goods.orderstate="3";
          }else if(irv=="-103"){
              Toast({message: "购买时间超过7天,申请退款失效!",iconClass: "iconfont icon-tishi1",duration: 2000});
              this.goods.iscanreturn=false;
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    },
    cencelHandle(event) {
      event.stopPropagation();
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type:'exitexitgoods',
          buyid:this.goods.buyid,
          serialnum:this.goods.serialnum
        })
        .then(response => {
          let irv = response.body.error.toString();
          if(irv=="ok"){
            this.goods.orderstate="0";
            this.goods.iscanreturn=response.body.arr.iscanreturn;
          }else if(irv=="-102"){
              Toast({message: "已经退款成功!",iconClass: "iconfont icon-tishi1",duration: 2000});
              this.goods.orderstate="3";
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.meal-cell-up {
    width: 100%;
    height: 30px;
    line-height: 30px;
    color: #42495C;

    .icon-face {
        padding-left: 10px;
        font-size: 20px;
    }

    .meal-name {
        font-size: 14px;
        word-wrap: break-word;
        font-weight: 700;
    }

    .icon-toright {
        font-size: 13px;
        color: #acacac;
        font-weight: 700;
    }
}

.meal-cell-middle {
    margin: 0px 12px 0px 0px;
    position: relative;
    background-color: #efefef;
    border-radius: 0px 15px 0px 0px;

    .cell-info {
        font-size: 0px;
        display: flex;
        padding: 7px 0px 11px 0px;
        align-items: center;
        min-height 60px;

        .cell-info-left {

            .my-img-meal {
                margin 0 6px 0 8px;
                width: 98px;
                height: 55px;
                background-color #f1f1f1;
            }
            .my-img-common{
                width: 60px;
                height: 60px;
                background-color #f1f1f1;
                margin 0 10px 0 10px;
            }
        }

        .cell-info-right {
            flex: 1;

            .dec {
                margin-top: 2px;
                font-size: 13px;
                margin-bottom: 3px;
                color: #42495C;
            }

            .buy-time {
                font-size: 12px;
                line-height: 14px;
                color: #acacac;
                margin-bottom: 2px;
            }

            .medal {
                display: inline-block;
                color: #FD8709;
                font-size: 12px;
                padding: 1px 4px;
                border-radius: 4px;
                border: 1px #FD8709 solid;
                margin-right: 2px;
            }
        }
    }

    .right-float {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);
        text-align: center;

        .price {
            color: #FD8709;
            font-size: 15px;
        }
    }
}

.meal-cell-down {
    width: 100%;
    height: 40px;
    text-align: right;
    position: relative;

    .middle-item{
        display inline-block;
        right: 12px;
        top: 50%;
        transform: translate(0, -50%);
        position: absolute;
        &.had-used{
            color #999;
            font-size 13px;
        }
    }

    .click-item{
        color #222;
        font-size 12px;
        line-height 12px;
        padding: 6px; 
        border-radius 15px;
        border 1px #acacac solid;

        &.left{
            right: 95px;
        }
        &.dark {
            color: #999;
        }
    }

    .tip {
        color: #8a8a8a;
        font-size: 12px;
        font-weight: 700;
        padding-right: 5px;
    }

}
</style>



