<template>
    <div ref="scrollContent" class="meal-list">
        <div class="meal-content">
            <meal-cell  class="meal-cell" v-for="dataItem in goodsArr" :goods="dataItem" :goodId="dataItem.buyid" @click.native="cellClickHandle">
            </meal-cell>
        </div>
        <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { formatDate } from "../../../common/js/date.js";
import EmptyBg from "../../popupBox/emptyBg.vue";
import mealCell from "../../public/mealCell.vue";
export default {
  name: "meal-list",
  data() {
    return {
      goodsArr: [],
      showEmpty: false,
      emptyMsg: "您还未购买过套餐!",
      memoryScroll:0,
      isLoadDb:false
    };
  },
  components: {
    EmptyBg,
    mealCell
  },
  created() {
    this.showEmpty = false;
    this.getGoodsData();
  },
  activated() {
    if(this.$refs.scrollContent){
      this.$refs.scrollContent.scrollTop=this.memoryScroll;
    }
    if(this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  beforeRouteLeave(to, from, next) {
    this.memoryScroll=this.$refs.scrollContent.scrollTop;
    next();
  },
  methods: {
    cellClickHandle(event) {
      let curTarget = event.currentTarget;
      let goodId = curTarget.getAttribute("goodId");
      this.$router.push({ name: "mealTicketList",params: {"lookMealId": goodId}});
    },
    getGoodsData() {
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "buylist",
          user: this.userid,
          goodstype:"1",
          page:0
        })
        .then(response => {
          let data = response.body;
          let budArr = [];
          data.forEach((val, index, arr) => {
            let tempDate = new Date(val.buydate * 1000);
            let buyTime = formatDate(tempDate, "yyyy-MM-dd");
            budArr.push({
              id: val.id.toString(),
              buyid:val.buyid.toString(),
              names: val.setsname,
              url: this.path + val.icon,
              buytm: buyTime,
              passtm:buyTime,
              price: val.price,
              dec:val.message,
              hadUesed:(val.orderstate.toString()=="2"),
              hadGive:(val.givestate.toString()=="1"),
              giveNum:val.phone,
              giveName:val.username,
              orderstate:val.orderstate.toString(),        //orderstate 0未使用、1使用中、2已经使用过、3申请退款、4已经退款
              iscanreturn:val.iscanreturn,                  //iscanreturn为true表示在退款的时间之内
              serialnum:val.serialnum,
              type:val.type                    //10为套餐
            });
          });
          this.goodsArr = budArr;
          if (this.goodsArr.length == 0) this.showEmpty = true;
          this.$store.dispatch("toggleLoading", false);
          this.isLoadDb=true;
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          this.$store.dispatch("toggleLoading", false);
        });
    }
  },
  computed: {
    ...mapState(["path", "userid","role"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.meal-list {
    height: 100%;

    .meal-content {
        width: 100%;

        .meal-cell{
            width:100%;
            background-color: #fff;
            margin-bottom 7px;
        }
    }
}
</style>



